import { Colored, Main, PageWrapper, Section } from "../components/utils";
import Header from '../components/Header';
import BlockQuote from '../components/BlockQuote';
import Checkbox from "../components/input/Checkbox";
import { useState } from "react";

export default function Home() {
  const [selected, setSelected] = useState<string | null | number>(null);
  return (
    <PageWrapper>
      <Header />
      <Main>
        <BlockQuote
          author={'Lolrus, the bucket walrus'}
          source={'At the launch of AWS S3 in 2006'}
        >
          <>
            Hosting a
            <Colored> webapp </Colored>
            or need to store uploads? Put them in S3 buckets. It&apos;s
            <Colored> cheap </Colored>
            and
            <Colored> reliable</Colored>
            . Let me show how
            <Colored> easy </Colored>
            it is.
          </>
        </BlockQuote>
      </Main>
      <Section>
        <h2>Do you want to fish or hunt?</h2>
        <Checkbox checked={selected == 'fish'} onChange={(v, c) => { if (c) { setSelected(v) } else {setSelected(null) }}} value={'fish'} label={'Fish'} />
        <Checkbox checked={selected == 'hunt'} onChange={(v, c) => { if (c) { setSelected(v) } else {setSelected(null) }}} value={'hunt'} label={'Fish'} />
      </Section>
    </PageWrapper>
  );
}
